@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
@font-face {
  font-family: "Steppe";
  src: url(../../static/media/Steppe.6302dc83.woff2) format("woff2"),
         url(../../static/media/Steppe.b2e88961.woff) format("woff");
 }
:root{
  --white-text: #ffffff;
  --header-font-family: 'Neucha', cursive;
  --text-font-family: 'Steppe', sans-serif;
}


html,body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

#root{
  width: 100%;
  height: 100%;
}
.App {
  text-align: center;
  background-image: url(../../static/media/bg.22ad0824.jpg);
  min-width: 100%;
  min-height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.favorite-button{
  background: none;
  border: none;
  outline: none;
  /* position: absolute;
  bottom: 15px */
  opacity: 0.5;
}
.favorite-button svg{
  width: 30px;
  height: 30px;
  fill: goldenrod;
}
.favorite-button:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* .favorite-button__on svg{
  fill:goldenrod;
} */
.toy-card{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 220px;
  height: 340px;
  padding: 15px;
  box-sizing: border-box;
  background: radial-gradient(109.56% 109.56% at 0% -2.94%, rgba(255, 255, 255, 0.5) 0%, rgba(179, 246, 255, 0.5) 100%);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
  border-radius: 10px;
  font-family: var(--text-font-family);
  color:var(--white-text);
  text-align: left;
}
.toy-card__header{
  font-family: var(--header-font-family);
  font-size: 18px;
  line-height: 20px;
  text-align: left;
}

.toy-card__image{
  max-width: 85px;
  max-height: 105px;
}

.toy-card__properties-list{
  list-style: none;
  padding: 0;
  width: 100%;
  margin-bottom: 5px;
}

.toy-card__properties-list-item{
  font-size: 10px;
  margin: 6px 0;
}
.toys-container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-gap:36px;
  gap:36px;
}
